html {
  width: 100%;
  max-width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

body {
    box-sizing: border-box;
    margin: 0 auto;
    height: 100%;
    width: 100%;
    max-width: 100%;
    padding: 0;
  }

  .visually-hidden {
    position: absolute;
  
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
  
    white-space: nowrap;
  
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
  }

  a {
    text-decoration: none;
  }
  